<script>
import {
  creditList,
  bigeventCompanyList,
  delCredit,
} from '../../config/api'
import {
  dialogConfirm,
  dialogConfirmHasbtn
} from '../../config/mUtils'
export default {
  data() {
    return {
      hasData: true, //是否有帐户数据
      datalist: [],
      searchText: '',
      pageSize: 10,
      currentPage: 1,
      total: null,
      layout: 'sizes,prev,pager,next',
      selectAll: false,
      watchID: '',
      companyList: [],
      totalCompanyList: [],
      loading: false,
      companyId: '',
      canClickAll: false,
    }
  },
  created() {
  	if (this.$route.query.page) {
  		this.currentPage = parseInt(this.$route.query.page);
  	}
    this.getDatalist();
    this.getCompanyList();

  },
  computed: {
    selectedPeople: function() {
      let sp = [];
      this.datalist.forEach(item => {
        if (item.checked) {
          sp.push(item.id);
        }
      })
      return sp;
    }
  },
  methods: {
    initData() {
      this.currentPage = 1;
      this.getDatalist()
      // this.canClickAllMethod();
    },
    //选择公司
    getCompanyList() {
      //获取公司列表
      bigeventCompanyList().then(res => {
        if (res.data.msgNo == 1) {
          this.totalCompanyList = res.data.payload;
          this.companyList = this.totalCompanyList.slice(0, 20);
        }
      })
    },
    //公司搜索
    remoteMethod(query) {
      if (query !== '') {
        this.loading = true;
        setTimeout(() => {
          this.loading = false;
          this.companyList = this.totalCompanyList.filter(item => {
            return item.name.indexOf(query) > -1;
          });
        }, 200);
      } else {
        this.companyList = [];
      }
    },

    getDatalist() {
      // console.log(this.companyId);
      let params = {
        number: this.pageSize,
        page: this.currentPage,
        search: this.searchText
      };
      if (this.companyId != '') {
        params.companyId = this.companyId;
      }
      creditList(params).then(res => {
        if (res.data.msgNo == 1) {
          let dl = res.data.payload.data;
          dl.forEach(item => {
            item.checked = false;
            // item.status = '有风险'
          })
          this.datalist = dl;
          if (this.searchText == '' && this.companyId == '' && this.datalist.length == 0) {
            this.hasData = false;
          } else {
            this.hasData = true;
          }
          this.total = res.data.payload.total
          this.canClickAllMethod();
        }
      })
    },
    handleSizeChange(val) {
      this.pageSize = val;
      this.getDatalist();
    },
    handleCurrentChange(val) {
      if (this.total > this.pageSize) {
        this.currentPage = val;
        this.getDatalist();
      }

    },
    //disable
    canClickAllMethod() {
      let count = 0;
      this.datalist.forEach(item => {
        if (item.status == "查询失败" || item.status == "查询中") {
          count++
        }
        if (count != 0 && count == this.datalist.length) {
          this.canClickAll = true;
        }
      })
    },
    //全选按钮
    selectallclick(e) {
      // console.log(e);
      this.datalist.forEach(item => {
        if (item.status != "查询失败" && item.status != "查询中") {
          item.checked = e
        }
      })
    },
    //单选按钮
    selectoneclick() {
      let count = 0;
      this.datalist.forEach(item => {
        if (item.checked) {
          count++
        }
      })
      if (count != 0 && count == this.datalist.length) {
        this.selectAll = true
      } else {
        this.selectAll = false
      }
    },
    //前往查询页添加页
    gotoAdd() {
      this.$router.push({
        name: 'creditQueryAdd'
      })
    },
    //去删除
    toDelete(id) {
      // let _that = this
      dialogConfirmHasbtn('确定要删除该条征信查询信息？').then(() => {
        delCredit({
          id: id
        }).then(res => {
          if (res.data.msgNo == 1) {
            this.getDatalist();
            dialogConfirm('success', '删除成功');
          } else {
            dialogConfirm('info', res.data.message);
          }
        })
      }).catch(() => {});

    },
    //单个查看
    toWatch(id) {
      console.log(id);
      this.$router.push({
        name: 'creditQueryView',
        query: {
          id: id,
          page: this.currentPage
        }
      })
    },
    //多项查看
    toWatchMany() {
      if (this.selectedPeople.length == 1) {
        this.$router.push({
          name: 'creditQueryView',
          query: {
            id: this.selectedPeople[0],
          }
        })
      } else if (this.selectedPeople.length > 1) {
        let idArr = JSON.parse(JSON.stringify(this.selectedPeople));
        this.$router.push({
          name: 'creditQueryView',
          query: {
            id: idArr.join(','),
          }
        })
      } else {
        dialogConfirm('info', '请先选择征信人')
      }
    },
    // 全选 （lz）
    handleSelectionChange(e, index) {
      e.forEach((res, idx) => {
        if(res.status != "查询失败" && res.status != "查询中") {
          res.checked = true;
        }
      })
      console.log(e, index);
    }
  }
};

</script>
